import React from "react";
import { localStorageConstants } from "../constants";

export type User = {
  id: string;
  name: string;
  email: string;
  profile_pic: string;
  gender: string;
  message: string;
  username: string;
  about: string;
  location: string;
};
interface AuthContextType {
  user: User | null;
  token: string | null;
  signin: (user: User, token: string) => void;
  isLoggedIn: boolean;
  signout: () => void;
  setUserDetails: (user: User) => void;
}

let AuthContext = React.createContext<AuthContextType>(null!);

export function AuthProvider({ children }: { children: React.ReactNode }) {
  let [user, setUser] = React.useState<User | null>(null);
  let [isLoggedIn, setisLoggedIn] = React.useState<boolean>(false);
  let [token, setToken] = React.useState<string | null>(null);
  let signin = (user: User, token: string) => {
    setUser(user);
    setToken(token);
    setisLoggedIn(true);
  };
  let setUserDetails = (newUser: User) => {
    setUser({ ...user, ...newUser });
  };
  let signout = () => {
    localStorage.removeItem(localStorageConstants.USER_TOKEN);
    setUser(null);
    setToken(null);
    setisLoggedIn(false);
  };

  let value = { user, signin, signout, token, setUserDetails, isLoggedIn };

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
}

export function useAuth() {
  return React.useContext(AuthContext);
}
