const LoginForm = (props: IAuthComponent) => {
  const { changeAuth, onSubmit } = props;
  const { loginUser, loading, error } = useLogin();
  const nav = useNavigate();
  const { signin } = useAuth();
  useEffect(() => {
    if (typeof error === "string") {
      toast.error(error);
    }
  }, [error]);
  return (
    <Formik
      initialValues={{
        email: "",
        password: "",
      }}
      validationSchema={LoginSchema}
      onSubmit={async (evt) => {
        const loginResponse = await loginUser(evt.email, evt.password);
        if (loginResponse.statusCode === 200) {
          const user = {
            id: loginResponse.id,
            email: loginResponse.email,
            gender: loginResponse.gender,
            message: loginResponse.message,
            name: loginResponse.name,
            profile_pic: loginResponse.profile_pic,
            username: loginResponse.username,
            about: loginResponse.about,
            location: loginResponse.location,
          };
          localStorage.setItem(
            localStorageConstants.USER_TOKEN,
            loginResponse.token
          );
          localStorage.setItem(
            localStorageConstants.USER_DETAIL,
            JSON.stringify(user)
          );
          toast.success("Welcome back!");
          signin(user, loginResponse.tokne);
          nav("/");
        }
      }}
      component={(formikProps) => {
        return (
          <Form className="basis-[70%] flex flex-col justify-between pt-5">
            <div className="flex flex-col justify-between px-8 lg:px-0">
              <AppInput
                name="email"
                onChange={formikProps.handleChange("email")}
                type="email"
                label="Email"
                value={formikProps.values.email}
                error={formikProps.errors.email}
              />
              <AppInput
                name="password"
                onChange={formikProps.handleChange("password")}
                label="Password"
                type="password"
                value={formikProps.values.password}
                error={formikProps.errors.password}
              />
            </div>
            <div>
              <div className="flex flex-col lg:flex-row justify-between lg:items-center px-10 lg:px-0 lg:pl-4">
                <div className="py-2">
                  <div className="flex items-start mr-4 mb-2">
                    <Field type="checkbox" name="checked" />
                    <label
                      htmlFor="A3-yes"
                      className="font-aileron select-none text-black text-xs lg:text-sm ml-4"
                    >
                      Remember Me
                    </label>
                  </div>
                </div>
                <button
                  onClick={() => changeAuth("forgot")}
                  className="lg:mx-0 font-aileron bg-orange text-white font-bold rounded-full py-3 px-4 lg:px-8 shadow-lg focus:outline-none focus:shadow-outline transform transition hover:scale-105 duration-300 ease-in-out text-xs"
                >
                  Forgot Password?
                </button>
              </div>
              <div className="flex flex-col lg:flex-row justify-between lg:items-center px-10 lg:px-0 lg:pl-4 mt-5 lg:mt-0">
                <button
                  disabled={loading}
                  type="submit"
                  className="font-aileron flex flex-initial lg:w-5/12 lg:mx-0 bg-primary uppercase text-white text-center justify-center font-bold rounded-full py-3 px-2 lg:px-8 shadow-lg focus:outline-none focus:shadow-outline transform transition duration-300 ease-in-out text-xs disabled:cursor-not-allowed"
                >
                  {loading ? <img className="h-6 w-6" src={loader} /> : "Login"}
                </button>
                <div
                  onClick={() => changeAuth("register")}
                  className="font-aileron text-black flex flex-wrap justify-center text-xs mt-2 lg:mt-0"
                >
                  Don't have an account?
                  <span className="font-aileron text-orange hover:cursor-pointer">
                    &nbsp; Cllct Account?
                  </span>
                </div>
              </div>
            </div>
          </Form>
        );
      }}
    />
  );
};

const OtpLogin = (props: IAuthComponent) => {
  const [otpSent, toggleOtpSent] = useState(false);
  const { changeAuth } = props;
  const [email, setEmail] = useState("");
  const [otp, setOtp] = useState("");
  const [emailError, setEmailError] = useState("");
  const { signin } = useAuth();
  const nav = useNavigate();
  const validateEmail = () => {
    return String(email)
      .toLowerCase()
      .match(
        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      );
  };
  const { error, loading, sendOtp } = useSendOtp();
  const validateOtpValues = useValidateOtp();
  useEffect(() => {
    if (typeof error === "string") {
      toast.error(error);
    }
    if (typeof validateOtpValues.error === "string") {
      toast.error(validateOtpValues.error);
    }
  }, [error, validateOtpValues.error]);
  const sendOtpCall = async () => {
    if (validateEmail()) {
      setEmailError("");
      const sendOtpResponse = await sendOtp(email);
      if (sendOtpResponse) {
        toggleOtpSent(true);
        toast.success("OTP sent to your email id");
      } else {
        toast.error("Something went wrong. Please try again");
      }
    } else {
      setEmailError("Please enter valid email");
    }
  };
  const validateOtpFunc = async () => {
    if (otp.length === 4) {
      const validateOtpResp = await validateOtpValues.validateOtp(email, otp);
      if (validateOtpResp.statusCode === 200) {
        const user = {
          id: validateOtpResp.id,
          email: validateOtpResp.email,
          gender: validateOtpResp.gender,
          message: validateOtpResp.message,
          name: validateOtpResp.name,
          profile_pic: validateOtpResp.profile_pic,
          username: validateOtpResp.username,
          about: validateOtpResp.about,
          location: validateOtpResp.location,
        };
        localStorage.setItem(
          localStorageConstants.USER_TOKEN,
          validateOtpResp.token
        );
        localStorage.setItem(
          localStorageConstants.USER_DETAIL,
          JSON.stringify(user)
        );
        toast.success("Welcome back!");
        signin(user, validateOtpResp.tokne);
        nav("/");
      }
    } else {
      toast.error("Please enter valid otp");
    }
  };
  return (
    <div className="basis-[70%] justify-between flex flex-col pt-5">
      <div className="flex flex-col justify-center px-8 lg:px-0 pt-6 md:pt-0">
        <AppInput
          name="email"
          placeholder="Email"
          label="Email"
          type="email"
          disabled={otpSent}
          onChange={(e) => setEmail(e.currentTarget.value)}
          error={emailError}
        />
        <div
          className={`${
            otpSent ? "visible" : "invisible"
          } flex flex-col pl-4 justify-center`}
        >
          <span className="font-aileron text-black">
            Enter the OTP you received at above email
          </span>
        </div>
        <div
          id="otp"
          className={`${
            otpSent ? "visible" : "invisible"
          } flex flex-row justify-start text-center px-2`}
        >
          <OTPInput
            autoFocus
            isNumberInput
            length={4}
            onChangeOTP={(otp) => setOtp(otp)}
          />
        </div>
      </div>
      <div>
        <div className="px-10 lg:pl-4">
          <div className="flex flex-col lg:flex-row justify-between lg:items-center px-2 lg:px-0 lg:pl-4 mt-5">
            <div className="py-2">
              <div className="flex items-start mr-4 mb-2">
                <input className="lg:mt-1" type="checkbox" name="checked" />
                <label
                  htmlFor="A3-yes"
                  className="font-aileron select-none text-black text-xs lg:text-sm ml-4"
                >
                  Remember Me
                </label>
              </div>
            </div>
            {otpSent && (
              <button
                onClick={() => sendOtpCall()}
                className="lg:mx-0 font-aileron bg-orange text-white font-bold rounded-full py-3 px-4 lg:px-8 shadow-lg focus:outline-none focus:shadow-outline transform transition hover:scale-105 duration-300 ease-in-out text-xs"
              >
                Resend OTP
              </button>
            )}
          </div>
        </div>
        <div className="flex flex-col lg:flex-row justify-between lg:items-center px-10 lg:px-0 lg:pl-4 mt-5">
          {loading || validateOtpValues.loading ? (
            <button
              disabled
              className="font-aileron flex flex-initial lg:w-5/12 lg:mx-0 bg-primary uppercase text-white text-center justify-center font-bold rounded-full py-2 px-2 lg:px-8 shadow-lg focus:outline-none focus:shadow-outline transform transition duration-300 ease-in-out text-xs disabled:cursor-not-allowed"
            >
              <img className="h-6 w-6" src={loader} />
            </button>
          ) : (
            <button
              disabled={otpSent ? otp.length !== 4 : false}
              onClick={() => (!otpSent ? sendOtpCall() : validateOtpFunc())}
              className="disabled:cursor-not-allowed disabled:hover:scale-100 flex font-aileron flex-initial lg:w-5/12 lg:mx-0 bg-primary uppercase text-white text-center justify-center font-bold rounded-full py-3 px-4 lg:px-8 shadow-lg focus:outline-none focus:shadow-outline transform transition hover:scale-105 duration-300 ease-in-out text-xs"
            >
              {otpSent ? "Login" : "Send OTP"}
            </button>
          )}

          <div className="font-aileron text-black flex flex-wrap justify-center text-xs mt-2 lg:mt-0">
            Don't have an account?
            <span
              onClick={() => changeAuth("register")}
              className="font-aileron text-orange hover:cursor-pointer"
            >
              &nbsp; Cllct Account?
            </span>
          </div>
        </div>
      </div>
    </div>
  );
};

const ForgotPassword = (props: IAuthComponent) => {
  const { changeAuth } = props;
  const { error, forgotPassword, loading } = useForgotPassword();
  useEffect(() => {
    if (typeof error === "string") {
      toast.error(error);
    }
  }, [error]);
  return (
    <div className="bg-white 2xl:w-[600px] xl:w-[600px] lg:w-3/4 md:w-3/4 w-full rounded-3xl lg:p-5 py-10 h-auto flex flex-col self-center lg:self-stretch">
      <div onClick={() => changeAuth("login")} className="flex items-center">
        <ArrowLeftIcon className="h-4 w-4 text-black cursor-pointer" />
        <p className="font-poppins text-black ml-3">Forgot Password</p>
      </div>
      <p className="font-poppins text-black text-sm my-5 px-2 md:px-5">
        Enter the email associated with your account and we'll send an email
        with instructions to reset your password.
      </p>
      <Formik
        initialValues={{ email: "" }}
        onSubmit={async (data) => {
          const resp = await forgotPassword(data.email);
          if (resp) {
            toast.success(
              "We've sent you an email regarding instructions to change password"
            );
            changeAuth("login");
          } else {
            toast.error("Something went wrong. Please try again");
          }
        }}
        validationSchema={LoginOtpEmailSchema}
        component={(formikProps: {
          handleChange: ChangeEventHandler<HTMLInputElement> | undefined;
          values: { email: string | number | readonly string[] | undefined };
          errors: { email: string | undefined };
        }) => {
          return (
            <Form>
              <AppInput
                name="email"
                placeholder="Email"
                label="Email"
                type="email"
                onChange={formikProps.handleChange}
                value={formikProps.values.email}
                error={formikProps.errors.email}
              />
              <div className="md:pl-5 flex justify-start">
                {loading ? (
                  <button
                    disabled
                    className="w-2/6 flex justify-center font-aileron bg-orange text-white font-bold rounded-full py-3 shadow-lg focus:outline-none focus:shadow-outline transform transition hover:scale-103 duration-300 ease-in-out text-xs"
                  >
                    <img className="h-6 w-6" src={loader} />
                  </button>
                ) : (
                  <button
                    type="submit"
                    className="w-2/6 font-aileron bg-orange text-white font-bold rounded-full py-3 shadow-lg focus:outline-none focus:shadow-outline transform transition hover:scale-103 duration-300 ease-in-out text-xs"
                  >
                    Submit
                  </button>
                )}
              </div>
            </Form>
          );
        }}
      />
    </div>
  );
};
