type A = React.DetailedHTMLProps<
  React.InputHTMLAttributes<HTMLInputElement>,
  HTMLInputElement
>;

interface IAppInputProps extends A {
  label: string;
  error?: string;
  showLabel?: boolean;
}
export const AppInput = (props: IAppInputProps) => {
  const { label, error, showLabel = true } = props;
  return (
    <div className="w-full mb-2 relative lg:pl-4">
      <div className="flex items-stretch">
        {showLabel && (
          <div className="hidden lg:flex lg:w-1/4 -mr-px">
            <div className="w-full flex items-center leading-normal bg-light rounded-l-2xl border border-r-0 border-grey-light px-3 text-xs font-poppins text-white">
              {label}
            </div>
          </div>
        )}

        <input
          className="form-control text-black font-poppins flex-shrink flex-grow flex-auto leading-normal w-px border h-10 border-grey-light rounded-2xl lg:rounded-l-none px-3 relative focus:border-0 active:border-0 bg-[#ECECEC] invalid:border-pink-500 invalid:text-pink-600"
          placeholder={label}
          {...props}
        />
      </div>
      <small
        className={`${
          error ? "text-red-600" : "text-white"
        } text-xs font-poppins`}
      >
        {error ? error : "Hiding the error"}
      </small>
    </div>
  );
};
