import create from "zustand";
type Option = { label: string; value: string };
type Theme = "light" | "dark";
export type Category = {
  title: string;
  id: string;
  status: number;
  show_menu: number;
};
interface LayoutStore {
  layout: Option;
  zoom: Option;
  sort: Option;
  theme: Theme;
  setLayout: (value: Option) => void;
  setSort: (value: Option) => void;
  setZoom: (value: Option) => void;
  setTheme: (value: Theme) => void;
  categories: Category[];
  setCategories: (data: Category[]) => void;
  selectCategory: (selectedCategory: Category) => void;
  selectedCategory: Category;
}
export const useLayoutStore = create<LayoutStore>()((set) => ({
  layout: { label: "Grid View", value: "grid" },
  zoom: { label: "100%", value: "100" },
  sort: { label: "Latest", value: "latest" },
  theme: "light",
  categories: [],
  selectedCategory: { id: "0", title: "All", show_menu: 1, status: 1 },
  setCategories: (data: Category[]) => set(() => ({ categories: data })),
  setLayout: (value: Option) =>
    set(() => ({
      layout: value,
    })),
  setSort: (value: Option) =>
    set(() => ({
      sort: value,
    })),
  setZoom: (value: Option) =>
    set(() => ({
      zoom: value,
    })),
  setTheme: (value: Theme) =>
    set(() => ({
      theme: value,
    })),
  selectCategory: (selectedCategory: Category) =>
    set(() => ({
      selectedCategory,
    })),
}));
