import { useQuery } from "@apollo/client";
import { useEffect } from "react";
import { useLocation } from "react-router-dom";
import { localStorageConstants } from "../constants";
import { useAuth } from "../context/AuthContext";
import { GET_CATEGORIES, GET_COLLECTIONS } from "../graphql/queries";
import { useCollectionStore } from "../store/collection.store";
import { useLayoutStore } from "../store/layout.store";
import Footer from "./Footer";
import Header from "./Header";

const Layout = ({ children }: { children: JSX.Element }) => {
  const { signin } = useAuth();
  const { setCategories, sort, selectedCategory } = useLayoutStore();
  const { setCollection } = useCollectionStore();
  useEffect(() => {
    const token = localStorage.getItem(localStorageConstants.USER_TOKEN);
    const user = localStorage.getItem(localStorageConstants.USER_DETAIL);
    if (token && user) {
      signin(JSON.parse(user), token);
    }
  }, []);
  const { data, loading } = useQuery(GET_CATEGORIES);
  const collectionResponse = useQuery(GET_COLLECTIONS, {
    variables: { sort_by: sort.value, category_id: "" },
  });
  useEffect(() => {
    collectionResponse.refetch({
      sort_by: sort.value,
      category_id: selectedCategory.id,
    });
  }, [sort, selectedCategory]);
  const location = useLocation();
  useEffect(() => {
    setCategories(data?.categorylist);
    if (!!collectionResponse?.data?.collectionlistwithfilter) {
      setCollection(collectionResponse?.data?.collectionlistwithfilter);
    }
  }, [data, collectionResponse]);
  return (
    <div>
      <Header />
      {children}
      <Footer />
    </div>
  );
};

export default Layout;
