export function formatLikes(count: any) {
  return (parseFloat(count) / 10000).toFixed(0);
}
export function formatViews(count: any) {
  return `${(parseFloat(count) / 100000000).toFixed(0)} k`;
}
export function formatRating(count: any) {
  return (parseFloat(count) / 100000).toFixed(0);
}
export function getColor() {
  return {
    background: "#" + ((Math.random() * 0xffffff) << 0).toString(16),
  };
}
export type Card = {
  image: any;
  from: string;
  title: string;
  description: string;
  tags: string[];
  owner: string;
  isGroup: boolean;
  category: string;
  likes: number;
  views: number;
  rating: number;
  timesRated: number;
  height?: boolean;
};
