import { Listbox, Popover, Switch, Transition } from "@headlessui/react";
import { CheckIcon, SelectorIcon } from "@heroicons/react/solid";
import { Formik, FormikProps } from "formik";
import { Fragment, useEffect } from "react";
import { toast, ToastContainer } from "react-toastify";
import { useAuth } from "../context/AuthContext";
import { useCreateCollection } from "../graphql/hooks";
import { Category, useLayoutStore } from "../store/layout.store";
import { CreateCollectionSchema } from "../utils/validators";
import loader from "../assets/loader.svg";
import axios from "axios";
import { useNavigate } from "react-router-dom";
export const CreatePanel = () => {
  const { user, isLoggedIn } = useAuth();
  const { categories } = useLayoutStore();
  const { createCollection, loading, error } = useCreateCollection();
  const nav = useNavigate();
  useEffect(() => {
    if (typeof error === "string") {
      toast.error(error);
    }
  }, [error]);
  return (
    <>
      {isLoggedIn ? (
        <Popover className={"relative"}>
          {({ open, close }) => (
            <>
              <Popover.Button
                className={`${
                  open
                    ? "text-gray-900 dark:text-white"
                    : "text-gray-500 dark:text-white"
                } px-3 py-1 group bg-white dark:bg-transparent dark:text-white rounded-md inline-flex items-center text-base font-medium hover:text-gray-900 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500`}
              >
                <div className="relative transform transition hover:scale-105 duration-300 ease-in-out">
                  <button className="font-aileron text-sm uppercase text-center w-24 h-9 font-semibold text-black rounded-3xl bg-[#FBBB0B]">
                    create
                  </button>
                  <div className="absolute cursor-pointer w-6 h-6 text-center text-white bg-gray-700 bg-opacity-75 rounded-full -right-3 -top-0 ring-white ring-1">
                    +
                  </div>
                </div>
              </Popover.Button>
              <Popover.Panel className="absolute z-10 left-1/3 transform -translate-x-1/2 mt-3 px-2 w-100 max-w-md sm:px-0">
                <div className="rounded-lg bg-white shadow-lg ring-1 ring-black ring-opacity-5 overflow-hidden">
                  <Formik
                    initialValues={{
                      image: null,
                      title: "",
                      desc: "",
                      tagsTitle: "",
                      category: categories?.[0],
                      tags: [],
                      public: false,
                    }}
                    validationSchema={CreateCollectionSchema}
                    onSubmit={async (data) => {
                      var imageData = new FormData();
                      //@ts-ignore
                      imageData.append("files", data.image);
                      imageData.append("isMultiple", "false");
                      const imageRes = await axios.post(
                        "https://api.pollhole.com/v1/uploadfile",
                        imageData
                      );
                      console.log("imageRes", imageRes);
                      let imageLink = "";
                      if (
                        imageRes.status === 200 &&
                        imageRes.data.status === 200
                      ) {
                        imageLink = imageRes.data.data[0];
                      }
                      const payload = {
                        image: imageLink,
                        title: data.title,
                        description: data.desc,
                        category_id: data.category.id,
                        tags: data.tagsTitle
                          .split(",")
                          .map((t) => `#${t.trim()}`),
                        creater_id: user?.id,
                      };
                      const createResponse = await createCollection(
                        payload.image,
                        payload.title,
                        payload.description,
                        payload.category_id,
                        payload.tags,
                        payload.creater_id
                      );
                      toast.success("Collection added successfully!");
                      close();
                    }}
                    component={(
                      formikProps: React.PropsWithChildren<
                        FormikProps<{
                          image: File | null;
                          title: string;
                          desc: string;
                          tagsTitle: string;
                          category: Category;
                          tags: never[];
                          public: boolean;
                        }>
                      >
                    ) => {
                      return (
                        <form
                          className="p-4"
                          onSubmit={formikProps.handleSubmit}
                        >
                          <div className="mb-3">
                            <h5 className="font-aileron font-bold text-black text-sm mb-1 capitalize">
                              Image
                            </h5>
                            <input
                              // value={formikProps.values.image}
                              className="w-full text-xs form-control text-black font-poppins flex-shrink flex-grow flex-auto leading-normal shadow-md h-10 border-[#707070] rounded-lg px-3 relative focus:border-0 active:border-0 bg-transparent invalid:border-pink-500 invalid:text-pink-600"
                              name={"image"}
                              type="file"
                              onChange={(e) =>
                                formikProps.setFieldValue(
                                  "image",
                                  e?.target?.files?.[0]
                                )
                              }
                            />
                            <small className="text-xs text-red-500">
                              {formikProps.errors.image}
                            </small>
                          </div>
                          <div className="mb-3">
                            <h5 className="font-aileron font-bold text-black text-sm mb-1 capitalize">
                              Title
                            </h5>
                            <input
                              value={formikProps.values.title}
                              className="w-full text-xs form-control text-black font-poppins flex-shrink flex-grow flex-auto leading-normal shadow-md h-10 border-[#707070] rounded-lg px-3 relative focus:border-0 active:border-0 bg-transparent invalid:border-pink-500 invalid:text-pink-600"
                              name={"title"}
                              onChange={formikProps.handleChange}
                            />
                            <small className="text-xs text-red-500">
                              {formikProps.touched.title &&
                                formikProps.errors.title}
                            </small>
                          </div>
                          <div className="mb-3">
                            <h5 className="font-aileron font-bold text-black text-sm mb-1 capitalize">
                              Description
                            </h5>
                            <textarea
                              value={formikProps.values.desc}
                              className="w-full text-xs form-control text-black font-poppins flex-shrink flex-grow flex-auto leading-normal shadow-md  h-24 border-[#707070] rounded-lg px-3 relative focus:border-0 active:border-0 bg-transparent invalid:border-pink-500 invalid:text-pink-600"
                              name={"desc"}
                              onChange={formikProps.handleChange}
                            />
                            <small className="text-xs text-red-500">
                              {formikProps.touched.desc &&
                                formikProps.errors.desc}
                            </small>
                          </div>
                          <Listbox
                            value={formikProps.values.category}
                            onChange={(value) =>
                              formikProps.setFieldValue("category", value)
                            }
                          >
                            <h5 className="font-aileron font-bold text-black text-sm mb-1 capitalize">
                              Category
                            </h5>
                            <div className="relative mt-1 mb-3">
                              <Listbox.Button className="relative w-full cursor-default rounded-lg bg-white py-2 pl-3 pr-10 text-left shadow-md focus:outline-none focus-visible:border-indigo-500 focus-visible:ring-2 focus-visible:ring-white focus-visible:ring-opacity-75 focus-visible:ring-offset-2 focus-visible:ring-offset-orange-300 sm:text-sm">
                                <span className="block truncate">
                                  {formikProps?.values?.category?.title ??
                                    "Select Category"}
                                </span>
                                <span className="pointer-events-none absolute inset-y-0 right-0 flex items-center pr-2">
                                  <SelectorIcon
                                    className="h-5 w-5 text-gray-400"
                                    aria-hidden="true"
                                  />
                                </span>
                              </Listbox.Button>
                              <Transition
                                as={Fragment}
                                leave="transition ease-in duration-100"
                                leaveFrom="opacity-100"
                                leaveTo="opacity-0"
                              >
                                <Listbox.Options className="absolute z-10 mt-1 max-h-60 w-full overflow-auto rounded-md bg-white py-1 text-base shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none sm:text-sm">
                                  {categories.map((category, personIdx) => (
                                    <Listbox.Option
                                      key={personIdx}
                                      className={({ active }) =>
                                        `relative cursor-default select-none py-2 pl-10 pr-4 ${
                                          active
                                            ? "bg-amber-100 text-amber-900"
                                            : "text-gray-900"
                                        }`
                                      }
                                      value={category}
                                    >
                                      {({ selected }) => (
                                        <>
                                          <span
                                            className={`block truncate ${
                                              selected
                                                ? "font-medium"
                                                : "font-normal"
                                            }`}
                                          >
                                            {category.title}
                                          </span>
                                          {selected ? (
                                            <span className="absolute inset-y-0 left-0 flex items-center pl-3 text-amber-600">
                                              <CheckIcon
                                                className="h-5 w-5"
                                                aria-hidden="true"
                                              />
                                            </span>
                                          ) : null}
                                        </>
                                      )}
                                    </Listbox.Option>
                                  ))}
                                </Listbox.Options>
                              </Transition>
                            </div>
                          </Listbox>
                          <div className="mb-3">
                            <h5 className="font-aileron font-bold text-black text-sm mb-1 capitalize">
                              # Tags
                            </h5>
                            <input
                              value={formikProps.values.tagsTitle}
                              className="w-full text-xs form-control text-black font-poppins flex-shrink flex-grow flex-auto leading-normal shadow-md h-10 border-[#707070] rounded-lg px-3 relative focus:border-0 active:border-0 bg-transparent invalid:border-pink-500 invalid:text-pink-600"
                              name={"tagsTitle"}
                              onChange={formikProps.handleChange}
                              placeholder="eg. Design,Art"
                            />
                            <small className="text-xs text-red-500">
                              {formikProps.touched.tagsTitle &&
                                formikProps.errors.tagsTitle}
                            </small>
                            {/* <div className="flex flex-wrap">
                            {formikProps.values.tags.map((t, i) => {
                              return (
                                <div
                                  key={i}
                                  className="flex capitalize text-[8px] m-2 bg-[#ECEFF4] p-2 rounded-lg shadow-md"
                                >
                                  #{t}&nbsp; &nbsp;
                                  <svg
                                    xmlns="http://www.w3.org/2000/svg"
                                    className="h-3 w-3"
                                    viewBox="0 0 20 20"
                                    fill="currentColor"
                                  >
                                    <path
                                      fillRule="evenodd"
                                      d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z"
                                      clipRule="evenodd"
                                    />
                                  </svg>
                                </div>
                              );
                            })}
                          </div> */}
                          </div>

                          <div className="mb-3 flex justify-end items-center">
                            <p className="text-sm text-[#707070]">
                              Visible to{" "}
                              <span className="text-pink-600">me</span>
                            </p>
                            <Switch
                              checked={formikProps.values.public}
                              onChange={(state: boolean) =>
                                formikProps.setFieldValue("public", state)
                              }
                              className={`${
                                formikProps.values.public
                                  ? "bg-blue-600"
                                  : "bg-gray-200"
                              } relative inline-flex h-6 w-11 items-center rounded-full ml-4`}
                            >
                              <span className="sr-only">Private to me</span>
                              <span
                                className={`${
                                  formikProps.values.public
                                    ? "translate-x-6"
                                    : "translate-x-1"
                                } inline-block h-4 w-4 transform rounded-full bg-white`}
                              />
                            </Switch>
                          </div>
                          <button
                            type="submit"
                            disabled={loading}
                            className="font-aileron w-full justify-center flex lg:mx-0 bg-primary text-white font-bold rounded-full py-3 px-4 lg:px-8 shadow-lg focus:outline-none focus:shadow-outline transform transition hover:scale-105 duration-300 ease-in-out text-xs"
                          >
                            {loading ? (
                              <img className="h-6 w-6" src={loader} />
                            ) : (
                              "Create"
                            )}
                          </button>
                          {!!formikProps.values.image && (
                            <div className="my-4">
                              <img
                                src={URL.createObjectURL(
                                  formikProps.values.image
                                )}
                                alt="new"
                              />
                              <h3 className="text-center">
                                {formikProps.values.title}
                              </h3>
                            </div>
                          )}
                        </form>
                      );
                    }}
                  />
                </div>
                <div className="rounded-lg shadow-lg ring-1 ring-black ring-opacity-5 overflow-hidden bg-white dark:bg-cardBg w-[250px]"></div>
              </Popover.Panel>
            </>
          )}
        </Popover>
      ) : (
        <div
          onClick={() => nav("/login")}
          className="relative transform transition hover:scale-105 duration-300 ease-in-out"
        >
          <button className="font-aileron text-sm uppercase text-center w-24 h-9 font-semibold text-black rounded-3xl bg-[#FBBB0B]">
            create
          </button>
          <div className="absolute cursor-pointer w-6 h-6 text-center text-white bg-gray-700 bg-opacity-75 rounded-full -right-3 -top-0 ring-white ring-1">
            +
          </div>
        </div>
      )}

      <ToastContainer />
    </>
  );
};
