import { Popover, RadioGroup } from "@headlessui/react";
import { Formik } from "formik";
import { useEffect, useState } from "react";
import { toast, ToastContainer } from "react-toastify";
import userIcon from "../assets/user-icon.svg";
import { useAuth, User } from "../context/AuthContext";
import { useUpdateUser } from "../graphql/hooks";
import { EditProfileSchema } from "../utils/validators";
import { AppInput } from "./AppInput";

export const UserPanel = () => {
  const { user, signout, setUserDetails } = useAuth();
  const [mode, setMode] = useState<"menu" | "profile" | "edit">("menu");
  const { updateUser, loading, error } = useUpdateUser();
  useEffect(() => {
    if (typeof error === "string") {
      toast.error(error);
    }
  }, [error]);
  return (
    <>
      <Popover className={"relative"}>
        {({ open }) => (
          <>
            <Popover.Button
              className={`${
                open
                  ? "text-gray-900 dark:text-white"
                  : "text-gray-500 dark:text-white"
              } px-3 py-1 group bg-white dark:bg-transparent dark:text-white rounded-md inline-flex items-center text-base font-medium hover:text-gray-900 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500`}
            >
              <div className="flex items-center gap-4">
                <div className="flex items-center justify-center rounded-full bg-[#E1E1E1] fill-white w-11 h-11">
                  <img src={userIcon} alt="userIcon" />
                </div>
                <h3 className="text-[#5E5C5C] dark:text-white text-sm font-semibold font-aileron">
                  {user?.name}
                </h3>
              </div>
            </Popover.Button>
            <Popover.Panel className="absolute z-10 left-1/2 transform -translate-x-1/2 mt-3 px-2 w-100 max-w-md sm:px-0">
              <div className="rounded-lg shadow-lg ring-1 ring-black ring-opacity-5 overflow-hidden">
                {mode === "menu" ? (
                  <>
                    <div className="relative bg-white dark:bg-cardBg px-2 py-2 sm:gap-2 sm:p-4 flex flex-col justify-center items-center text-center">
                      <img src="../../../resources/user-icon.svg" alt="" />
                      <p className="font-aileron text-black dark:text-white text-base">
                        {user?.name}
                      </p>
                      <p className="font-aileron text-black dark:text-white text-base">
                        {user?.location}
                      </p>
                    </div>
                    <div className="relative grid gap-6 bg-white dark:bg-cardBg dark:text-white px-2 py-2 sm:gap-3 sm:p-3">
                      <div
                        onClick={() => setMode("profile")}
                        className="ml-4 pr-4"
                      >
                        <p className="font-aileron text-xs font-medium text-gray-900 dark:text-white cursor-pointer">
                          Profile
                        </p>
                      </div>
                    </div>
                    <div className="relative grid gap-6 bg-white dark:bg-cardBg dark:text-white px-2 py-2 sm:gap-3 sm:p-3">
                      <div className="ml-4 pr-4">
                        <p className="font-aileron text-xs font-medium text-gray-900 dark:text-white cursor-pointer">
                          Subscription
                        </p>
                      </div>
                    </div>
                    <div className="relative grid gap-6 bg-white dark:bg-cardBg dark:text-white px-2 py-2 sm:gap-3 sm:p-3">
                      <div className="ml-4 pr-4">
                        <p className="font-aileron text-xs font-medium text-gray-900 dark:text-white cursor-pointer">
                          Payment
                        </p>
                      </div>
                    </div>
                    <div className="relative grid gap-6 bg-white dark:bg-cardBg px-2 py-2 sm:gap-3 sm:p-3">
                      <div className="ml-4 pr-4">
                        <p className="font-aileron text-xs font-medium text-gray-900 dark:text-white cursor-pointer">
                          Langugae
                        </p>
                      </div>
                    </div>
                    <div className="relative grid gap-6 bg-white dark:bg-cardBg dark:text-white px-2 py-2 sm:gap-3 sm:p-3">
                      <div className="ml-4 pr-4">
                        <p className="font-aileron text-xs font-medium text-gray-900 cursor-pointer">
                          Settings
                        </p>
                      </div>
                    </div>
                    <div
                      onClick={() => signout()}
                      className="relative grid gap-6 bg-white dark:bg-cardBg dark:text-white px-2 py-2 sm:gap-3 sm:p-3"
                    >
                      <div className="ml-4 pr-4">
                        <p className="font-aileron text-xs font-medium text-gray-900 cursor-pointer">
                          Logout
                        </p>
                      </div>
                    </div>
                  </>
                ) : (
                  <div className="relative bg-white p-5">
                    <div className="profile-nav-back flex justify-between items-center p-2 text-center dark:border-darkBorder">
                      <p onClick={() => setMode("menu")} className="text-black">
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          className="h-6 w-6 flex-0 fill-black dark:fill-white cursor-pointer"
                          viewBox="0 0 24 24"
                          stroke="currentColor"
                        >
                          <path
                            stroke-linecap="round"
                            strokeLinejoin="round"
                            stroke-width="2"
                            d="M10 19l-7-7m0 0l7-7m-7 7h18"
                          />
                        </svg>
                      </p>
                      <h5 className="m-0 flex-1 font-aileron text-black dark:text-white">
                        Profile
                      </h5>
                    </div>
                    {!!user && (
                      <Formik
                        initialValues={{
                          username: user.username,
                          name: user.name,
                          gender: user?.gender?.toLowerCase(),
                          about: user.about,
                          location: user.location,
                        }}
                        validationSchema={EditProfileSchema}
                        onSubmit={async (data) => {
                          const updateResponse = await updateUser(
                            data.name,
                            data.username,
                            data.about,
                            data.location,
                            null,
                            data.gender
                          );
                          setUserDetails({
                            name: updateResponse.name,
                            email: updateResponse.email,
                            about: updateResponse.about,
                            gender: updateResponse.gender,
                            id: updateResponse.id,
                            location: updateResponse.location,
                            profile_pic: updateResponse.profile_pic,
                            username: updateResponse.username,
                            message: "",
                          });
                          setMode("profile");
                          toast.success("Profile updated successfully!");
                        }}
                        component={(formikProps: any) => {
                          return (
                            <form onSubmit={formikProps.handleSubmit}>
                              {[...(Object.keys(user) as [keyof User])].map(
                                (k: keyof User, i) => {
                                  if (k === "gender") {
                                    return (
                                      <div
                                        key={i}
                                        className="mx-auto w-full max-w-md mb-3"
                                      >
                                        <h5 className="font-aileron text-[#7630B4] text-sm mb-1 capitalize">
                                          {k}
                                        </h5>
                                        <RadioGroup
                                          onChange={(value: string) => {
                                            formikProps.setFieldValue(
                                              "gender",
                                              value
                                            );
                                          }}
                                          value={formikProps.values.gender}
                                        >
                                          <RadioGroup.Label className="sr-only">
                                            Gender
                                          </RadioGroup.Label>
                                          <div className="flex flex-wrap justify-between">
                                            {[
                                              "male",
                                              "female",
                                              "rather not say",
                                            ].map((g, i) => {
                                              return (
                                                <RadioGroup.Option
                                                  key={i}
                                                  value={g}
                                                >
                                                  {({ active, checked }) => (
                                                    <div
                                                      className={`${
                                                        active
                                                          ? "ring-1 ring-offset-1 ring-offset-[#B163E2] ring-white ring-opacity-60"
                                                          : ""
                                                      } ${
                                                        checked
                                                          ? "bg-light bg-opacity-80 text-white "
                                                          : "bg-white"
                                                      } flex py-2 px-5 mt-3 w-auto rounded-lg shadow-md cursor-pointer focus:outline-none transition ease-in-out`}
                                                    >
                                                      <div className="flex items-center justify-between">
                                                        <div className="flex items-center">
                                                          {checked && (
                                                            <div className="flex-shrink-0 text-white mr-2">
                                                              <svg
                                                                className="w-3 h-3"
                                                                viewBox="0 0 24 24"
                                                                fill="none"
                                                              >
                                                                <circle
                                                                  cx="12"
                                                                  cy="12"
                                                                  r="12"
                                                                  fill="#fff"
                                                                  fill-opacity="0.2"
                                                                />
                                                                <path
                                                                  d="M7 13l3 3 7-7"
                                                                  stroke="#fff"
                                                                  stroke-width="1.5"
                                                                  stroke-linecap="round"
                                                                  strokeLinejoin="round"
                                                                />
                                                              </svg>
                                                            </div>
                                                          )}

                                                          <div className="text-xs">
                                                            <RadioGroup.Label
                                                              as="p"
                                                              className={`${
                                                                checked
                                                                  ? "text-white"
                                                                  : "text-gray-900"
                                                              } font-normal text-center font-aileron capitalize`}
                                                            >
                                                              {g}
                                                            </RadioGroup.Label>
                                                          </div>
                                                        </div>
                                                      </div>
                                                    </div>
                                                  )}
                                                </RadioGroup.Option>
                                              );
                                            })}
                                          </div>
                                        </RadioGroup>
                                        <small className="text-xs text-red-500">
                                          {formikProps.errors.gender
                                            ? formikProps.errors.gender
                                            : ""}
                                        </small>
                                      </div>
                                    );
                                  }
                                  if (
                                    k !== "id" &&
                                    k !== "profile_pic" &&
                                    k !== "message"
                                  )
                                    return (
                                      <div key={i} className="mb-3">
                                        <h5 className="font-aileron text-[#7630B4] text-sm mb-1 capitalize">
                                          {k}
                                        </h5>
                                        <input
                                          value={
                                            k === "email"
                                              ? user[k]
                                              : mode === "profile"
                                              ? user[k]
                                              : // @ts-ignore
                                                formikProps.values[k] ?? ""
                                          }
                                          disabled={
                                            mode === "profile" || k === "email"
                                          }
                                          className="w-full text-xs form-control text-black font-poppins flex-shrink flex-grow flex-auto leading-normal border h-10 border-grey-light rounded-2xl px-3 relative focus:border-0 active:border-0 bg-[#ECECEC] invalid:border-pink-500 invalid:text-pink-600"
                                          name={k}
                                          onChange={formikProps.handleChange}
                                        />
                                        <small className="text-xs text-red-500">
                                          {
                                            // @ts-ignore
                                            formikProps.errors[k]
                                              ? // @ts-ignore
                                                formikProps.errors[k]
                                              : ""
                                          }
                                        </small>
                                      </div>
                                    );
                                }
                              )}
                              <div className="edit-profile-btn text-center">
                                {mode === "edit" ? (
                                  <button
                                    type="submit"
                                    className="font-aileron inline-block lg:mx-0 bg-primary text-white font-bold rounded-full py-3 px-4 lg:px-8 shadow-lg focus:outline-none focus:shadow-outline transform transition hover:scale-105 duration-300 ease-in-out text-xs"
                                  >
                                    {!loading ? "Update Changes" : "Updating"}
                                  </button>
                                ) : (
                                  <button
                                    onClick={(e) => setMode("edit")}
                                    className="font-aileron inline-block lg:mx-0 bg-primary text-white font-bold rounded-full py-3 px-4 lg:px-8 shadow-lg focus:outline-none focus:shadow-outline transform transition hover:scale-105 duration-300 ease-in-out text-xs"
                                  >
                                    Edit Profile
                                  </button>
                                )}
                              </div>
                            </form>
                          );
                        }}
                      />
                    )}
                  </div>
                )}
              </div>

              <div className="rounded-lg shadow-lg ring-1 ring-black ring-opacity-5 overflow-hidden bg-white dark:bg-cardBg w-[250px]"></div>
            </Popover.Panel>
          </>
        )}
      </Popover>
      <ToastContainer />
    </>
  );
};
