import { useMutation, useQuery } from '@apollo/client';
import { localStorageConstants } from '../constants';
import { CREATE_COLLECTION, FORGOT_PASSWORD, GET_CATEGORIES, GET_SINGLE_COLLECTION, LOGIN, REGISTER_USER, SAMPLE_MUTATION, SAMPLE_QUERY, SEND_OTP, SOCIALLOGIN, UPDATER_USER, VALIDATE_OTP } from './queries';
const getAccessToken = () => {
  return localStorage.getItem(localStorageConstants.USER_TOKEN)
}
//sample query for reference only
export function useSampleQuery(id) {
  const { data, loading, error } = useQuery(SAMPLE_QUERY, {
    variables: { id },
  });
  return {
    company: data?.company,
    loading,
    error: Boolean(error),
  };
}

export function useGetCollection(id) {
  const { data, loading, error } = useQuery(GET_SINGLE_COLLECTION, {
    variables: { id },
  });
  return {
    details: data?.getcollectiondetail,
    loading,
    error: Boolean(error),
  };
}

