export function useUpdateUser() {
  const [mutate, { loading, error }] = useMutation(UPDATER_USER);
  return {
    updateUser: async (name, username, about, location, profile_pic, gender) => {
      const { data: { updateuser } } = await mutate({
        variables: {
          name,
          username,
          about,
          location,
          profile_pic,
          gender
        },
        context: {
          headers: { 'Authorization': 'Bearer ' + getAccessToken() },
        },
      })
      return updateuser
    },
    loading,
    error: error?.message,
  }
}

export function useCreateCollection() {
  const [mutate, { loading, error }] = useMutation(CREATE_COLLECTION);
  return {
    createCollection: async (image, title, description, category_id, tags, creater_id) => {
      const { data: { addcollection } } = await mutate({
        variables: {
          image, title, description, category_id, tags, creater_id
        },
        context: {
          headers: { 'Authorization': 'Bearer ' + getAccessToken() },
        }
      })
      return addcollection
    },
    loading,
    error: error?.message
  }
}