import * as Yup from "yup";
export const requiredMessage = (fieldName: string) =>
  `${fieldName} is required`;
export const validMessage = (fieldName: string) =>
  `Please enter valid ${fieldName}`;
export const minErrorMessage = (min: number) =>
  `Please enter atleast ${min} characters`;

export const LoginSchema = Yup.object().shape({
  email: Yup.string()
    .email(validMessage("email"))
    .required(requiredMessage("Email")),
  password: Yup.string().required(requiredMessage("Password")),
});
export const LoginOtpEmailSchema = Yup.object().shape({
  email: Yup.string()
    .email(validMessage("email"))
    .required(requiredMessage("Email")),
});
export const RegisterSchema = Yup.object().shape({
  name: Yup.string()
    .required(requiredMessage("Name"))
    .min(4, minErrorMessage(4)),
  username: Yup.string()
    .required(requiredMessage("Name"))
    .min(4, minErrorMessage(4)),
  email: Yup.string()
    .email(validMessage("email"))
    .required(requiredMessage("Email")),
  password: Yup.string()
    .required(requiredMessage("Password"))
    .min(8, minErrorMessage(8)),
});

export const CreateCollectionSchema = Yup.object().shape({
  title: Yup.string().required(requiredMessage("Title")),
  desc: Yup.string().required(requiredMessage("Description")),
  tagsTitle: Yup.string().required(requiredMessage("Tags")),
  category: Yup.object().required(requiredMessage("Category")),
});

export const EditProfileSchema = Yup.object().shape({
  name: Yup.string()
    .required(requiredMessage("Name"))
    .min(4, minErrorMessage(4)),
  username: Yup.string()
    .required(requiredMessage("Username"))
    .min(4, minErrorMessage(4)),
  gender: Yup.string().required(requiredMessage("Gender")),
});
