import { useState } from "react";
import { ToastContainer } from "react-toastify";
import loader from "../assets/blue-loader.svg";
import { CardViewCard } from "../components/CardViewCard";
import { CollectionDetailModal } from "../components/CollectionDetailModal";
import { GridCard } from "../components/GridViewCard";
import { ListTile } from "../components/ListViewTile";
import { useCollectionStore } from "../store/collection.store";
import { useLayoutStore } from "../store/layout.store";

const Home = () => {
  const { layout, sort } = useLayoutStore();
  const { collection, loading, setActiveId } = useCollectionStore();
  let [isOpen, setIsOpen] = useState(false);
  function closeModal() {
    setIsOpen(false);
  }

  function openModal(id: string) {
    setActiveId(id);
    setIsOpen(true);
  }

  return (
    <>
      {loading ? (
        <div className="flex w-full justify-center">
          <img
            src={loader}
            alt="Loader"
            className="w-60 h-20 flex justify-center"
          />
        </div>
      ) : (
        <>
          {layout.value === "card" ? (
            <div className="bg-white dark:bg-darkBg" id="square-grid">
              <div className="container mx-auto bg-white dark:bg-darkBg p-[2rem] pb-16">
                <div className="grid grid-cols-1 xl:grid-cols-5 lg:grid-cols-5 md:grid-cols-4 sm:md:grid-cols-2 gap-4">
                  {collection.map((card, index) => {
                    return (
                      <CardViewCard
                        onCardClick={(id) => openModal(id)}
                        item={card}
                        key={index}
                      />
                    );
                  })}
                </div>
              </div>
            </div>
          ) : layout.value === "grid" ? (
            <div id="regular-grid" className="bg-white dark:bg-darkBg">
              <div className="masonry-1-col md:masonry-2-col lg:masonry-5-col xl:masonry-5-col 2xl:masonry-6-col bg-white dark:bg-darkBg container mx-auto p-[2rem] pb-16">
                {collection.map((card, index) => {
                  return (
                    <GridCard
                      onCardClick={(id) => openModal(id)}
                      item={card}
                      index={index}
                      key={index}
                    />
                  );
                })}
              </div>
            </div>
          ) : (
            <div id="list-view" className="bg-white dark:bg-darkBg">
              <div className="container mx-auto bg-white dark:bg-darkBg p-[1rem] md:p-[2rem] pb-16">
                <div
                  id="list-view-header"
                  className="hidden items-center text-center text-white rounded-3xl px-2 sm:bg-[#6317A7] sm:h-12 sm:flex"
                >
                  <p
                    id="list-view-head-category"
                    className="font-aileron hidden text-left pl-2 sm:w-1/12 lg:flex lg:w-1/12"
                  >
                    Category
                  </p>
                  <p
                    id="list-view-head-title"
                    className="font-aileron hidden text-left pl-2 sm:flex sm:w-1/3 sm:justify-center lg:w-1/5"
                  >
                    Title
                  </p>
                  <p
                    id="list-view-head-tags"
                    className="hidden font-aileron sm:flex sm:w-1/3 sm:justify-center lg:w-1/4"
                  >
                    Tags
                  </p>
                  <p
                    id="list-view-head-name"
                    className="font-aileron hidden lg:flex lg:justify-center lg:w-1/12"
                  >
                    Name
                  </p>
                  <p
                    id="list-view-head-actions"
                    className="font-aileron hidden sm:flex sm:w-1/3 sm:justify-center lg:w-2/5"
                  >
                    Actions
                  </p>
                </div>
                {collection.map((card, index) => {
                  return (
                    <ListTile
                      onCardClick={(id) => openModal(id)}
                      item={card}
                      key={index}
                    />
                  );
                })}
              </div>
            </div>
          )}
        </>
      )}
      {isOpen && (
        <CollectionDetailModal isOpen={isOpen} closeModal={closeModal} />
      )}

      <ToastContainer />
    </>
  );
};
export default Home;
