import { Popover } from "@headlessui/react";

interface IHeaderDropdown {
  options: { label: string; value: string }[];
  selected: { label: string; value: string };
  label: string;
  onChange: (value: { label: string; value: string }) => void;
}
const HeaderDropdown = (props: IHeaderDropdown) => {
  const { label, onChange, options, selected } = props;
  return (
    <Popover className="relative">
      {({ open }) => (
        <div className="sort-filter-item mr-2.5">
          <Popover.Button
            className={`${
              open ? "text-gray-900" : "text-gray-500"
            } group bg-white dark:bg-transparent font-medium hover:text-gray-900 focus:outline-none text-right`}
          >
            <div className="flex justify-end pr-1 font-aileron text-xs text-grey dark:text-darkMuted font-light cursor-pointer text-right">
              {label}
            </div>
            <p className="cursor-pointer items-center text-grey dark:text-white font-aileron flex text-[14px] font-bold mt-2">
              {selected.label}
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="15"
                height="16"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="{2}"
                  d="M19 9l-7 7-7-7"
                />
              </svg>
            </p>
          </Popover.Button>
          <Popover.Panel className="absolute bg-white dark:bg-dropdownBg z-10 left-1/2 transform -translate-x-1/2 mt-3 w-28 text-center flex justify-center flex-col px-3">
            {options.map((opt, i) => {
              return (
                <Popover.Button
                  key={i}
                  onClick={() => onChange(opt)}
                  className="cursor-pointer text-black dark:text-white font-aileron my-2 text-sm"
                >
                  <p onClick={() => onChange(opt)}>{opt.label}</p>
                </Popover.Button>
              );
            })}
          </Popover.Panel>
        </div>
      )}
    </Popover>
  );
};
export const ResponsiveHeaderDropdown = (props: IHeaderDropdown) => {
  const { label, onChange, options, selected } = props;
  return (
    <Popover className="relative w-full">
      {({ open }) => (
        <>
          <Popover.Button
            className={`${
              open ? "text-gray-900" : "text-gray-500"
            } flex w-full py-2 border-r-[0.5px] border-neutral-800 justify-center items-center flex-col bg-grey dark:bg-cardBg font-medium hover:text-gray-900 text-center`}
          >
            <p className="flex font-aileron text-xs text-[#404040] dark:text-darkWhite cursor-pointer text-center">
              {label}
            </p>
            <div className="cursor-pointer flex items-center text-[#404040] mt-2">
              <p className="text-center font-aileron text-sm font-bold mr-2 dark:text-darkWhite">
                {selected.label}
              </p>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="15"
                height="16"
                className="fill-black dark:fill-white"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="{2}"
                  d="M19 9l-7 7-7-7"
                />
              </svg>
            </div>
          </Popover.Button>
          <Popover.Panel className="absolute bg-white dark:bg-dropdownBg z-10 mt-3 w-full text-center flex justify-center flex-col px-3">
            {options.map((opt, i) => {
              return (
                <Popover.Button
                  onClick={() => onChange(opt)}
                  v-for="item in options"
                  key={i}
                  className="cursor-pointer text-black dark:text-darkWhite font-aileron my-2 text-sm"
                >
                  <p onClick={() => onChange(opt)}>{opt.label}</p>
                </Popover.Button>
              );
            })}
          </Popover.Panel>
        </>
      )}

      {/* <transition
      enter-active-class="transition ease-out duration-200"
      enter-from-class="opacity-0 translate-y-1"
      enter-to-class="opacity-100 translate-y-0"
      leave-active-class="transition ease-in duration-150"
      leave-from-class="opacity-100 translate-y-0"
      leave-to-class="opacity-0 translate-y-1"
    >
      
    </transition> */}
    </Popover>
  );
};
export default HeaderDropdown;
