import { RadioGroup } from "@headlessui/react";
import { Formik } from "formik";
import { useNavigate } from "react-router-dom";
import { useAuth, User } from "../context/AuthContext";
import { useUpdateUser } from "../graphql/hooks";
import { EditProfileSchema } from "../utils/validators";
import { ToastContainer, toast } from "react-toastify";
import { useEffect } from "react";
const Profile = () => {
  const { user, setUserDetails } = useAuth();

  const { updateUser, loading, error } = useUpdateUser();
  const nav = useNavigate();
  useEffect(() => {
    if (typeof error === "string") {
      toast.error(error);
    }
  }, [error]);
  return (
    <div>
      <Formik
        initialValues={{
          username: user?.username,
          name: user?.name,
          gender: user?.gender?.toLowerCase(),
          about: user?.about,
          location: user?.location,
        }}
        validationSchema={EditProfileSchema}
        onSubmit={async (data) => {
          const updateResponse = await updateUser(
            data.name,
            data.username,
            data.about,
            data.location,
            null,
            data.gender
          );
          toast.success("Profile updated successfully!");
          setUserDetails({
            name: updateResponse.name,
            email: updateResponse.email,
            about: updateResponse.about,
            gender: updateResponse.gender,
            id: updateResponse.id,
            location: updateResponse.location,
            profile_pic: updateResponse.profile_pic,
            username: updateResponse.username,
            message: "",
          });
          nav("/");
        }}
        component={(formikProps: any) => {
          return (
            <form className="px-10" onSubmit={formikProps.handleSubmit}>
              {[...(Object.keys(user ?? {}) as [keyof User])].map(
                (k: keyof User, i) => {
                  if (k === "gender") {
                    return (
                      <div key={i} className="mx-auto w-full max-w-md mb-3">
                        <h5 className="font-aileron text-[#7630B4] text-sm mb-1 capitalize">
                          {k}
                        </h5>
                        <RadioGroup
                          onChange={(value: string) => {
                            formikProps.setFieldValue("gender", value);
                          }}
                          value={formikProps.values.gender}
                        >
                          <RadioGroup.Label className="sr-only">
                            Gender
                          </RadioGroup.Label>
                          <div className="flex flex-wrap justify-between">
                            {["male", "female", "rather not say"].map(
                              (g, i) => {
                                return (
                                  <RadioGroup.Option key={i} value={g}>
                                    {({ active, checked }) => (
                                      <div
                                        className={`${
                                          active
                                            ? "ring-1 ring-offset-1 ring-offset-[#B163E2] ring-white ring-opacity-60"
                                            : ""
                                        } ${
                                          checked
                                            ? "bg-light bg-opacity-80 text-white "
                                            : "bg-white"
                                        } flex py-2 px-5 mt-3 w-auto rounded-lg shadow-md cursor-pointer focus:outline-none transition ease-in-out`}
                                      >
                                        <div className="flex items-center justify-between">
                                          <div className="flex items-center">
                                            {checked && (
                                              <div className="flex-shrink-0 text-white mr-2">
                                                <svg
                                                  className="w-3 h-3"
                                                  viewBox="0 0 24 24"
                                                  fill="none"
                                                >
                                                  <circle
                                                    cx="12"
                                                    cy="12"
                                                    r="12"
                                                    fill="#fff"
                                                    fill-opacity="0.2"
                                                  />
                                                  <path
                                                    d="M7 13l3 3 7-7"
                                                    stroke="#fff"
                                                    stroke-width="1.5"
                                                    stroke-linecap="round"
                                                    strokeLinejoin="round"
                                                  />
                                                </svg>
                                              </div>
                                            )}

                                            <div className="text-xs">
                                              <RadioGroup.Label
                                                as="p"
                                                className={`${
                                                  checked
                                                    ? "text-white"
                                                    : "text-gray-900"
                                                } font-normal text-center font-aileron capitalize`}
                                              >
                                                {g}
                                              </RadioGroup.Label>
                                            </div>
                                          </div>
                                        </div>
                                      </div>
                                    )}
                                  </RadioGroup.Option>
                                );
                              }
                            )}
                          </div>
                        </RadioGroup>
                        <small className="text-xs text-red-500">
                          {formikProps.errors.gender
                            ? formikProps.errors.gender
                            : ""}
                        </small>
                      </div>
                    );
                  }
                  if (k !== "id" && k !== "profile_pic" && k !== "message")
                    return (
                      <div key={i} className="mb-3">
                        <h5 className="font-aileron text-[#7630B4] text-sm mb-1 capitalize">
                          {k}
                        </h5>
                        <input
                          value={
                            k === "email"
                              ? user?.[k]
                              : // @ts-ignore
                                formikProps.values[k] ?? ""
                          }
                          disabled={k === "email"}
                          className="w-full text-xs form-control text-black font-poppins flex-shrink flex-grow flex-auto leading-normal border h-10 border-grey-light rounded-2xl px-3 relative focus:border-0 active:border-0 bg-[#ECECEC] invalid:border-pink-500 invalid:text-pink-600"
                          name={k}
                          onChange={formikProps.handleChange}
                        />
                        <small className="text-xs text-red-500">
                          {
                            // @ts-ignore
                            formikProps.errors[k]
                              ? // @ts-ignore
                                formikProps.errors[k]
                              : ""
                          }
                        </small>
                      </div>
                    );
                }
              )}
              <div className="edit-profile-btn text-center">
                <button
                  type="submit"
                  className="font-aileron w-full inline-block lg:mx-0 bg-primary text-white font-bold rounded-full py-3 px-4 lg:px-8 shadow-lg focus:outline-none focus:shadow-outline transform transition hover:scale-105 duration-300 ease-in-out text-xs"
                >
                  {!loading ? "Update Changes" : "Updating"}
                </button>
              </div>
            </form>
          );
        }}
      />
      <ToastContainer />
    </div>
  );
};

export default Profile;
