import create from "zustand";
export type Collection = {
  id: string;
  title: string;
  slug: string;
  description: string;
  image: string;
  isTrending: boolean;
  tags: [string];
  creater_id: string;
  category_id: string;
  status: number;
  likes_count: number;
  view_count: number;
  statusCode: number;
  message: string;
  user: { name: string };
  category: { title: string };
};
interface CollectionStore {
  activeId: string;
  collection: Collection[];
  loading: boolean;
  setCollection: (collection: Collection[]) => void;
  setActiveId: (id: string) => void;
}
export const useCollectionStore = create<CollectionStore>()((set) => ({
  activeId: "",
  collection: [],
  loading: true,
  setCollection: (collection) =>
    set(() => ({
      collection,
      loading: false,
    })),
  setActiveId: (id) =>
    set(() => ({
      activeId: id,
    })),
}));
